<template>
    <span :class="`card ${tema.status}` " @click="chooseOne(tema.status)" :style="{backgroundColor: showColor(tema.status)}">
        <span>
            <b>{{ tema.nome }}</b>
        </span>
        <span class="icon-container">
            <Icon class="icon" :icon="tema.icon" />
        </span>
    </span>

</template>
  
<script>
 
   export default { 
 
       components: {   
       }, 
       props: { 
            tema:{
                type: Object,
                required: true,
                default: () => ({
                    nome: 'N/A',
                    icon: 'mdi:numeric-1-box',
                    status: 'locked'
                })
            }
       },
       data(){
           return{ 
           } 
       }, 
       methods: { 
        
        showColor(status){

            if(status === 'locked' || status === 'unavaliable'){
                return '#ccc'
            
            }else{
                return '#fff'
            }
        },
        chooseOne(status){
            if(status === 'locked' || status === 'unavaliable'){
                
                this.$swal.fire({
                    title: 'Tema bloqueado',
                    // text: 'Este tema ainda não está disponível',
                    toast: true,
                    timer: 2000,
                    timerProgressBar: true,
                    icon: 'warning',
                    position: 'bottom-end',
                    showConfirmButton: false,
                    
                })

            }else{
                this.$emit('picked', this.tema)
            }
        }
        
       }, 
       created(){
       } 

   }
</script>
 
<style scoped>

    .card .icon-container{
        /* background-color: red; */
        padding: 1em;
        
    }
    .card .icon-container .icon{
        width: 100%;
        height: 100%;
        color: yellowgreen;
    }

    .unlocked{
        background-color: #fff;
        cursor: pointer;
    }
    
    .unlocked:hover{
        transform: scale(1.01);
    }

    .locked, .unavaliable{
        background-color: #ccc;
        cursor: not-allowed;
    }

</style> 